body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@media only screen and (min-width: 800px) and (min-width: 1050px) {
  .top-50 {
    top: 20%!important;
  }
}
#gameContainer{
  max-width: 800px;
}
.progress {
  margin: 1%;
}
.progress {
  margin: 2%!important;
  margin-top: 0!important;
}
.dropdown-menu {
  font-size: 70%!important;
}
.dropdown-toggle{
  width: 100%;
}
.card{
  background-color: rgba(128 182 239 66)!important;
}
.card-footer button{
  background-color: #2356a0;
}

.start-100{left: 92%!important;}
.start-0{left: 3%!important;}
